import React from "react"
import { Link } from "gatsby"

import Layout from "../components/Layout"
import SEO from "../components/seo"

const DeliveryPage = () => (
  <Layout>
    <SEO title="Delivery" />
    <h1 className="display-1">Delivery</h1>
    <p className="lead">What we offer and how you can get it.</p>
    <p>
      We deliver authentic Chinese food in the area of Delft for reasonable prices.
      We do this by preparing food for lunch or dinner in large quantities, and then delivering it all at once.
    </p>
    <p>
      The menu is published a day ahead through Whatsapp and WeChat.
      Whatsapp and WeChat are also used to place your order.
    </p>
    <h2>Examples</h2>
    
    <Link to="/">Go back to the homepage</Link>
  </Layout>
)

export default DeliveryPage
